.@{container-class} .fn-m-job-arrow-bar{
	background:#fff;
	border-radius:5*@rex;
	margin:10*@rex 24*@rex 0 24*@rex;
	padding:10*@rex;

		display:flex;
		justify-content:space-between;

	.left{
		.iconfont{
			    vertical-align: baseline;
			    font-size:18*@rex;
		}
	}

	.right{
		.iconfont{position:relative; top: 4*@rex;}
	}
}