@import "./_job.less";
@import "./module/fn-m-job-button";
@import "./module/fn-m-job-arrow-bar";


.@{container-class} {
	.ui-wrap{
			background:#fff;
			margin:10*@rex 10*@rex 0 10*@rex;
			padding:15*@rex;
		}

	.personal-information{
		color:@secondary-color;


		h3{font-weight:700;color:#000;padding:10*@rex 0;}
		p{padding-bottom:10*@rex;}
		li{
			padding:10*@rex 0;
			border-top:1*@rex solid @border-color;
		}
		}

	.job-detal{
		.article{
			h3{padding:10*@rex 0;}
		}
	}

	.button-area{
		background:#fff;
		margin-top:10*@rex;
	}
}
